<template>
    <form @submit.prevent="joinForm">
        <h1>회원가입</h1>
        <div>
            <label>
                아이디
                <br />
                <input v-model="userId" type="text" placeholder="ID" autofocus />
            </label>
        </div>

        <div>
            <label>
                비밀번호
                <br />
                <input v-model="userPw" type="password" placeholder="PASSWORD" />
            </label>
        </div>

        <div>
            <label>
                이름
                <br />
                <input v-model="name" type="text" />
            </label>
        </div>

        <div>
            <label>
                전화번호
                <br />
                <input v-model="tel" type="text" />
            </label>
        </div>

        <div>
            <label>
                생일
                <br />
                <input v-model="birthday" type="text" />
            </label>
        </div>

        <div>
            <label>
                성별
                <br />
                <input v-model="gender" type="text" />
            </label>
        </div>

        <div>
            <label>
                우편번호
                <br />
                <input v-model="postcode" type="text" />
            </label>
        </div>

        <div>
            <label>
                주소
                <br />
                <input v-model="addr1" type="text" />
            </label>
        </div>

        <div>
            <label>
                상세주소
                <br />
                <input v-model="addr2" type="text" />
            </label>
        </div>
        
        <div>
            <button type="submit">가입</button>
        </div>
    </form>
    <button @click="cancel()">취소</button>
</template>

<script>
    import axios from 'axios';

    export default {
        data() {
            return {
                userInfo: {
                    userId : '',
                    userPw : '',
                    name : '',
                    tel : '',
                    birthday : '',
                    gender : '',
                    postcode : '',
                    addr1 : '',
                    addr2 : ''
                }
            };
        },
        methods : {
            async joinForm() {
                try {
                    const response = await axios.post('http://localhost:8095/api/user/regist', {
                        userId : this.userId,
                        passPw : this.userPw,
                        name : this.name,
                        tel : this.tel,
                        birthday : this.birthday,
                        gender : this.gender,
                        postcode : this.postcode,
                        addr1 : this.addr1,
                        addr2 : this.addr2
                    });
                    console.log("회원가입 성공!!", response.data);
                } catch (error) {
                    alert("회원가입 실패..");
                    console.log(error);
                }
            }
        }
    }
</script>